export default function VerticalLinearStepper(props) {
  const steps = props.data;
  const [activeStep, setActiveStep] = React.useState(0);

  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleReset = () => {
    setActiveStep(0);
  };

  return (
    <Box className="stepper">
      <Stepper activeStep={activeStep} orientation="vertical">
        {steps.map((step, index) => (
          <Step key={step.label}>
            <StepLabel
              optional={
                (index =
                  index.length - 1 ? <p>Hozircha so`nggi malumot</p> : null)
              }
            >
              <h2>{step.label} </h2>
            </StepLabel>
            <StepContent>
              <p>{step.description}</p>
              <Box sx={{ mb: 2 }}>
                <div>
                  <Button
                    variant="contained"
                    onClick={handleNext}
                    sx={{ mt: 1, mr: 1 }}
                    className="btn primary-color"
                  >
                    {index === steps.length - 1 ? "Finish" : "Continue"}
                  </Button>
                  <Button
                    className="btn primary-color"
                    disabled={index === 0}
                    onClick={handleBack}
                    sx={{ mt: 1, mr: 1 }}
                  >
                    Back
                  </Button>
                </div>
              </Box>
            </StepContent>
          </Step>
        ))}
      </Stepper>
      {activeStep === steps.length && (
        <Paper square elevation={0} sx={{ p: 3 }}>
          <p>Barcha qism bilan tanishdingiz</p>
          <Button
            className="btn primary-color"
            onClick={handleReset}
            sx={{ mt: 1, mr: 1 }}
          >
            Restart
          </Button>
        </Paper>
      )}
    </Box>
  );
}
