import React, { useEffect, useReducer } from "react";
import Loader from "./Loader";
import Button from "@mui/material/Button";
import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";
import Box from "@mui/material/Box";
import m1 from "./m3.png";
import m2 from "./m5.png";
import m3 from "./m4.png";
import m4 from "./m6.png";
import m5 from "./m5.png";
import AOS from "aos";
import sound from "./sound.mp3";
import "aos/dist/aos.css";
import { motion, AnimatePresence } from "framer-motion";
import { AnimateSharedLayout } from "framer-motion";
import Accordion from "./Accordion";
import GitHubIcon from "@mui/icons-material/GitHub";
import LinkIcon from "@mui/icons-material/Link";
import Modal from "./Modal";
import Modal2 from "./Modal2";
import resume from "./resume2.pdf";
const reducer = (state, action) => {
  switch (action.type) {
    case "loading":
      return { ...state, loading: false };
    case "filter":
      return { ...state, filter: action.data };
    case "modal":
      return { ...state, modal: !state.modal };
    case "modal2":
      return {
        ...state,
        tech: action.tech,
        modal2: !state.modal2,
        title: action.title,
        desc: action.desc,
      };
  }
};
export default function App() {
  useEffect(() => {
    AOS.init();
  }, []);
  const [state, disp] = useReducer(reducer, {
    loading: false,
    modal: false,
    title: "",
    desc: "",
    tech: "",
    data: [
      {
        name: "Leap English",
        desc: "This website is built for leap English learning centre, good animations, fully responsive",
        link: "https://www.leapenglish.uz/",
        img: m1,
        type: "layout general",
        tech: ["Html", "Css", "Js", "Animate"],
      },
      {
        name: "Online shop",
        img: m2,
        type: "react api general",
        link: "https://thirsty-noether-217ca0.netlify.app/",
        desc: "Sample online shop,fully responsive,easy to use + register and easy to order",
        tech: ["React", "Css", "Mui", "Aos", "O shop Api"],
      },
      {
        name: "Movie DataBase",
        img: m3,
        type: "react general",
        link: "https://resplendent-cocada-6d0647.netlify.app/",
        desc: "This app created with TMDB api,fully responsive,animated,useful and easy to use",
        tech: ["React", "Css", "Mui", "Aos", "TMDB"],
      },
      {
        name: "Test Builder",
        img: m4,
        type: "react hard general",
        link: "",
        desc: "Test builder app can help you to make different tests with a lot of options",
        tech: ["React", "Redux", "Css", "Mui"],
      },
      { name: "Currency Converter", img: m5, type: "js api general", link: "" },
    ],
    filter: [],
  });
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const filter = (e) => {
    const audio = new Audio();
    audio.src = sound;
    audio.play();
    let newArr = state.data.filter((event) => {
      return event.type.includes(e);
    });
    disp({ type: "filter", data: newArr });
  };
  useEffect(() => {
    let newArr = state.data.filter((event) => {
      return event.type.includes("general");
    });
    disp({ type: "filter", data: newArr });
  }, []);
  return (
    <div className="main-com">
      {state.modal && <Modal func={() => disp({ type: "modal" })} />}
      {state.loading && <Loader name="loader" />}
      {state.modal2 && (
        <Modal2
          title={state.title}
          desc={state.desc}
          func={() => disp({ type: "modal2" })}
          tech={state.tech}
        />
      )}
      {!state.loading && (
        <div className="wrapper">
          <div className="works">
            <p className="works-info">Ba'zi ishlarim</p>
            <Box
              sx={{
                width: "95%",
                marginTop: "25px",
                bgcolor: "#fff",
                borderRadius: "5px",
                padding: "0 10px",
              }}
            >
              <Tabs
                value={value}
                onChange={handleChange}
                variant="scrollable"
                scrollButtons="auto"
                allowScrollButtonsMobile="true"
                aria-label="scrollable auto tabs example"
              >
                <Tab
                  label="General"
                  className="tab"
                  onClick={() => filter("general")}
                />
                <Tab
                  label="Html css js "
                  className="tab"
                  onClick={() => filter("js")}
                />
                <Tab
                  label="React"
                  className="tab"
                  onClick={() => filter("react")}
                />
                <Tab
                  label="Api"
                  className="tab"
                  onClick={() => filter("api")}
                />
                <Tab
                  label="JWT"
                  className="tab"
                  onClick={() => filter("jwt")}
                />
                <Tab
                  label="Hard"
                  className="tab"
                  onClick={() => filter("hard")}
                />
                <Tab
                  label="page-proofs ()"
                  className="tab"
                  onClick={() => filter("layout")}
                />
                <Button
                  onClick={() => disp({ type: "modal" })}
                  style={{
                    fontWeight: "400",
                  }}
                  className="tab-btn"
                >
                  Contact
                </Button>
              </Tabs>
            </Box>
            <div className="items">
              <AnimatePresence>
                {state.filter.map((e) => (
                  <motion.div
                    key={e.name}
                    animate={{ opacity: 1 }}
                    initial={{ opacity: 0 }}
                    exit={{ opacity: 0 }}
                    layout
                    className="box"
                  >
                    <div className="flex flex-center img-center">
                      <img src={e.img} alt="" />
                    </div>
                    <div className="info">
                      <div>
                        <h2>{e.name}</h2>
                        <div
                          className="acc flex flex-center"
                          onClick={() => {
                            disp({
                              type: "modal2",
                              title: e.name,
                              desc: e.desc,
                              tech: e.tech,
                            });
                          }}
                        >
                          <p>About</p>
                        </div>
                        <div className="flex-btn-group">
                          <Button className="btn2">
                            Github <GitHubIcon className="icon" />
                          </Button>
                          <Button
                            className="btn2"
                            onClick={() => window.open(e.link)}
                          >
                            Link <LinkIcon className="icon" />
                          </Button>
                        </div>
                      </div>
                    </div>
                  </motion.div>
                ))}
              </AnimatePresence>
            </div>
          </div>
        </div>
      )}
      <h1></h1>
    </div>
  );
}
