AOS.init();

const reducer = (state, action) => {
  switch (action.type) {
    case "mobMenu":
      return { ...state, mobMenu: !state.mobMenu };
    case "inpmodal":
      return { ...state, inpModal: !state.inpModal };
    case "check":
      return { ...state, check: action.data };
    case "edit":
      return { ...state, [action.name]: action.text };
    case "clear":
      return { ...state, text: "", desc: "", num: "" };
    case "modal":
      return { ...state, modal: !state.modal };

    default:
      return state;
  }
};

